@import "../../main.scss";
.navigation-nav {
  margin: 40px 100px;
  display: flex;
  gap: 15px;
  align-items: center;
  flex-wrap: wrap;
  color: black;
  @include themed() {
    color: t("whitecolor");
  }
}

@media screen and (max-width: 1280px) {
  .navigation-nav {
    font-size: 18px;
  }
}
@media screen and (max-width: 1024px) {
  .navigation-nav {
    font-size: 16px;
  }
}
@media screen and (max-width: 940px) {
  .navigation-nav {
    margin: 40px 50px;
    font-size: 14px;
    gap: 10px;
    img {
      width: 7px;
    }
  }
}

@media screen and (max-width: 512px) {
  .navigation-nav {
    margin: 40px 30px;
    font-size: 12px;
    gap: 7px;
    img {
      width: 5px;
    }
  }
}
