@import "../../main.scss";
.home-top {
  display: flex;
  margin-top: 20px;
  .home-left {
    padding-left: 135px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    flex: 1;
    h2 {
      color: black;
      @include themed() {
        color: t("whitecolor");
      }
    }
    .hand-icon {
      display: flex;
      align-content: center;
      flex-direction: row;
      align-items: center;
      gap: 10px;
      img {
        width: 100px;
      }
    }
    p {
      font-size: 100px;
      font-weight: 600;
      @include themed() {
        color: t("whitecolor");
      }
    }
    .latest-btn {
      display: flex;
      margin-top: 20px;
      background-color: red;
      width: 311px;
      height: 60px;
      gap: 10px;
      align-content: center;
      align-items: center;
      border-radius: 50px;
      justify-content: center;
      cursor: pointer;
      .collection {
        color: white;
        font-size: 22px;
        font-weight: 600;
      }
      img {
        width: 24px;
      }
    }
  }
  .home-right {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex: 1;
    img {
      filter: drop-shadow(0 0px 5px rgba(0, 0, 0, 1.1));
    }
  }
}

@media screen and (max-width: 1280px) {
  .home-top {
    .home-left {
      padding-left: 100px;
      .hand-icon {
        img {
          width: 80px;
        }
      }
      p {
        font-size: 60px;
      }
      .latest-btn {
        width: 251px;
        height: 55px;
        .collection {
          font-size: 20px;
          font-weight: 500;
        }
        img {
          width: 22px;
        }
      }
    }
    .home-right {
      img {
        width: 500px;
      }
    }
  }
}

